.NavSuper {
  display: flex;
  justify-content: center;
  background: #000;
  border-bottom: rgb(90, 77, 0) solid 1px;
}

.NavContainer {
  height: 90px;
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 3rem;
  padding-right: 3rem;
  align-items: center;
  font-size: 0.8rem;
  position: sticky;
  top: 0;
  z-index: 10;
  color: rgb(255, 255, 255);

  /* border: white solid 1px; */
}

.NavLogos {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  width: 250px;

  /* border: white solid 1px; */
}

.NavRightPanel {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 55%;

  /* border: white solid 1px; */
}

.Navigation {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-right: 30px;
  width: 65%;
  color: gray;

  /* border: rgb(150, 28, 28) solid 2px; */
}

.NavSocials {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 28%;
  padding-left: 40px;
  border-left: aqua solid 4px;

  /* border: rgb(49, 206, 67) solid 2px; */
}

.NavbtnCTA {
  width: 90px;
  height: 40px;
  border: solid aqua 3px;
  color: white;
  background-color: rgb(0, 0, 0);
  border-radius: 5px;
  box-shadow: inset 0 0 0 0 rgb(242, 184, 49);
  transition: ease-out 0.3s;
  font-size: 1.4rem;
  outline: none;
  cursor: pointer;
}
